



















































































.article-list-simple {
    .search {
        border-bottom: 1px solid #f7f7f7;
        padding: 20px 0;
        display: flex;

        button {
            flex: 0 0 100px;
            margin-left: 10px;
        }
    }

    .list {
        min-height: calc(100vh - 143px);

        .item {
            border-bottom: 1px solid #f7f7f7;
            padding: 10px 0;

            &:hover {
                border-bottom: 1px solid #ccc;
            }

            .info {
                font-size: 14px;
                color: #999;

                .tag {
                    color: #ff0068;

                    &:after {
                        content: '';
                        background-color: #999;
                        border-radius: 50%;
                        width: 2px;
                        height: 2px;
                        display: inline-block;
                        vertical-align: middle;
                        margin: 0 4px;
                    }
                }

                .time {
                    font-size: 12px;
                }
            }

            .title {
                font-size: 20px;
                color: #333;
                margin: 10px 0;
                display: block;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;

                &:hover {
                    color: #000;
                }
            }

            .operate-info {
                display: flex;

                button {
                    border-left: none;
                    padding: 3px 10px;

                    i {
                        display: inline-block;
                        font-size: 14px;
                    }

                    i, span {
                        vertical-align: middle;
                        color: #999;
                        line-height: 1;
                    }

                    &:nth-child(1) {
                        border-left: 1px solid #ccc;
                    }

                    &:hover {
                        background-color: #ccc;

                        i, span {
                            color: #fff;
                        }
                    }
                }
            }
        }
    }

    .pager {
        margin-top: 30px;
    }
}
